import Multiselect from 'multiselect-react-dropdown'
import { nanoid } from 'nanoid'
import { CompactPicker } from 'react-color'
import addImage from '../../../Assets/images/add-image.png'
import AdminSideBar from '../../../Components/AdminPage/AdminSideBar/AdminSideBar'
import CartPageButton from '../../../Components/CartPage/CartPageButton'
import SectionContainer from '../../../Components/Utilities/09-SectionContainer/SectionContainer'
import AdminEditProductPageHook from '../../../Hooks/Product/AdminEditProductPageHook'
import Spinner from "react-bootstrap/Spinner";
import './AdminEditProductPage.css'

const AdminEditProductPage = () => {
  const [productName, productDescription, productPriceBeforeDiscount, productPrice, productAvailableQuantity, productNameOnChangeHandle, productDescriptionOnChangeHandle, productPriceBeforeDiscountOnChangeHandle, productPriceOnChangeHandle, productAvailableQuantityOnChangeHandle, mainCategoryData, brandData, productMainCategory, productSubCategory, productBrand, productMainCategoryOnChangeHandle, productSubCategoryDependsOnMain, productSubCategoryOnSelectHandle, productSubCategoryOnRemoveHandle, productBrandOnChangeHandle, colorInputOnchangeHandle, colorValues, productColorOnClickHandle, fileInputOnChangeHandle, images, imageOnClickHandle, addColorIconOnClickHandle, showColorPicker, saveBtnOnClickHandle, mainCategoryDataIsLoaded, brandDataIsLoaded, loader] = AdminEditProductPageHook()

  const options = productSubCategoryDependsOnMain

  return (
    <SectionContainer>
      <div className="container mt-5">
        <div className="row">
          <div className="col-lg-3">
            <AdminSideBar></AdminSideBar>
          </div>
          <div className="col-lg-9">
            <div className="admin-header">
              <h5>  </h5>
            </div>
            <div className="admin-content">
              <div className="admin-add-product">
                <div className="col-lg-10 col-xl-8">
                  <p className="text-secondary">  (   5  -      ) <br /> *    </p>
                  <div className="product-images">
                    {
                      images ? (images.map((item, i)=>{
                        return <img key={nanoid()} src={images[i]} alt="" title="" onClick={()=>{imageOnClickHandle(i)}} />
                      })) : null
                    }
                    <label htmlFor="upload-image" className="upload-image">
                    <img src={addImage} alt="" />
                    
                  </label>
                  <input type="file" alt="" id="upload-image" className="d-none" multiple onChange={fileInputOnChangeHandle}/>
                  </div>
                  <input type="text" placeholder=" " onChange={productNameOnChangeHandle} value={productName} />
                  <textarea placeholder=" " onChange={productDescriptionOnChangeHandle} value={productDescription}></textarea>
                  <input type="number" placeholder="  " onChange={productPriceOnChangeHandle} value={productPrice} />
                  <input type="number" placeholder="   " onChange={productPriceBeforeDiscountOnChangeHandle} value={productPriceBeforeDiscount} />
                  <input type="number" placeholder="  " onChange={productAvailableQuantityOnChangeHandle} value={productAvailableQuantity} />
                  <select id="admin-add-product" name="admin-add-product" onChange={productMainCategoryOnChangeHandle} value={productMainCategory} >
                    <option value="0"> </option>
                    {
                      mainCategoryDataIsLoaded && mainCategoryData.data ? (mainCategoryData.data.map((item)=>{
                        return <option key={item._id} value={item._id}>{item.name}</option>
                      })) : <option >  </option>
                    }
                  </select>


                  <Multiselect
                  options={options}
                  placeholder = " "
                  multi = {true}
                  searchable = {true}
                  closeOnScroll = {true}
                  direction = "ltr"
                  className="admin-add-product__multi-select"
                  onSelect={productSubCategoryOnSelectHandle}
                  onRemove={productSubCategoryOnRemoveHandle}
                  displayValue="name" 
                  selectedValues = {productSubCategory}
                  />


                  <select id="admin-add-product" name="admin-add-product" onChange={productBrandOnChangeHandle} value={productBrand} > 
                    <option value="0"></option>
                    {
                      brandDataIsLoaded && brandData.data ? (brandData?.data.map((item)=>{
                        return <option key={item._id} value={item._id}>{item.name}</option>
                      })) : <option>  </option>
                    }
                  </select>
                  <div className="admin-add-product__color">
                    <h6>  </h6>
                    <div className="d-flex align-items-center">
                      {colorValues ? ( colorValues.map((colorItem)=>{ 
                        return <span key={nanoid()} className="shadow" style={{backgroundColor: colorItem}} onClick={()=>{
                          productColorOnClickHandle(colorItem)
                        }} ></span>
                      })) : null}
                      <i className={`fa-solid fa-circle-plus ${!showColorPicker ? `d-block` : `d-none`}`} onClick={addColorIconOnClickHandle}></i>
                      <i className={`fa-solid fa-circle-xmark ${showColorPicker ? `d-block` : `d-none`}`} onClick={addColorIconOnClickHandle}></i>
                      <div className={`color-picker mt-2 ${!showColorPicker ? `d-none` : null}`}>
                    </div>
                    </div>
                  </div>
                  <div className={`color-picker mt-2 ${!showColorPicker ? `d-none` : null}`}>
                      <CompactPicker onChangeComplete = {colorInputOnchangeHandle} />
                  </div>
                  <div className="admin-add-product__btn">
                    <CartPageButton>
                      <button onClick={saveBtnOnClickHandle}>
                         
                        <Spinner animation="border" className={`addBrand-spinner ${!loader ? `d-none` : null}`} />
                      </button>
                    </CartPageButton>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </SectionContainer>
  )
}

export default AdminEditProductPage