import "./SortBar.css";

const SortBar = (props) => {
  return (
    <div className="sort-bar container d-flex justify-content-between align-items-center">
      <p className="m-0">{props.productsCount}  </p>
      <div className="sort-bar-dropdown">
        <div className="dropdown">
          <button type="button" data-bs-toggle="dropdown">
            <i className="fas fa-filter"></i>
             
          </button>
          <ul className="dropdown-menu shadow">
            <li>
              <h5 className="dropdown-header"></h5>
            </li>
            <li className="dropdown-item sort-bar-dropdown-item" onClick={()=> props.topLeftSortLiOnClickHandle("+price")} >  </li>
            <li className="dropdown-item sort-bar-dropdown-item" onClick={()=> props.topLeftSortLiOnClickHandle("-price")}>  </li>
            <li>
              <h5 className="dropdown-header"></h5>
            </li>
            <li className="dropdown-item sort-bar-dropdown-item" onClick={()=> props.topLeftSortLiOnClickHandle("-sold")}> </li>
            <li className="dropdown-item sort-bar-dropdown-item" onClick={()=> props.topLeftSortLiOnClickHandle("-ratingQuantity")}>   </li>
            <li className="dropdown-item sort-bar-dropdown-item" onClick={()=> props.topLeftSortLiOnClickHandle("-updatedAt")}>   </li>
            <li className="dropdown-item sort-bar-dropdown-item" onClick={()=> props.topLeftSortLiOnClickHandle("+updatedAt")}>   </li>
            <h5 className="dropdown-header"> </h5>
            <li className="dropdown-item sort-bar-dropdown-item" onClick={()=> props.topLeftSortLiOnClickHandle("")}>   </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default SortBar;
