import FormButtonStyle from "../02-FormButtonStyle/FormButtonStyle";
import "./LoginRegisterForm.css";

const LoginRegisterForm = (props) => {  
  return (
    <div className="login-form m-auto my-3 col-11 col-sm-8 col-md-6 col-lg-4">
      <form action="" className="d-flex flex-column">
        {props.BtnTitle === "  " && (<input type="text" name="" id="text" placeholder=" " onChange={props.userNameInputOnChangeHandle} value={props.userNameInput} ref={props.userNameInputElementRef} />)}
        <input type="email" name="" id="email" placeholder="" onChange={props.emailInputOnChangeHandle} value={props.emailInput} ref={props.emailInputElementRef} />
        {props.BtnTitle === "  " && (<input type="tel" name="" id="tel" placeholder="" onChange={props.phoneInputOnChangeHandle} value={props.phoneInput} ref={props.phoneInputElementRef} />)}
        <input type="password" name="" id="password" placeholder=" " onChange={props.passwordInputOnChangeHandle} value={props.passwordInput} ref={props.passwordInputElementRef} />
        {props.BtnTitle === "  " && (<input type="password" name="con-password" id="" placeholder="  " onChange={props.passwordConfirmationInputOnChangeHandle} value={props.passwordConfirmationInput} ref={props.passwordConfirmationInputElementRef} />)}
        <FormButtonStyle formButtonOnClickHandle={props.formButtonOnClickHandle} BtnTitle={props.BtnTitle}>{props.children}</FormButtonStyle>
      </form>
    </div>
  );
};

export default LoginRegisterForm;
